import React, { useCallback, useState } from "react";
import { Button, Grid, TextField } from "@mui/material";

//todoList  
const TodoInsert = ({ onInsert }) => {
  const [value, setValue] = useState("");

  const onChange = useCallback((e) => {
    setValue(e.target.value);
  }, []);

  const onSubmit = useCallback(
    (e) => {
      onInsert(value);
      setValue("");
      e.preventDefault();
    },
    [onInsert, value]
  );

  const onKeyDown = (e) => {
    if (e.key === "Enter") {
      onSubmit();
    }
  };

  return (
    <Grid
      container
      flexWrap="nowrap"
      className="modalHeader"
      sx={{
        gap: "20px",
        position: "sticky",
        zIndex: "999",
        opacity: 1,
      }}
    >
      <TextField
        label="   ."
        fullWidth
        variant="standard"
        onChange={onChange}
        value={value}
        onKeyDown={onKeyDown}
      />
      <Button
        onClick={onSubmit}
        type="submit"
        variant="outlined"
        sx={{ height: "45px" }}
      >
        
      </Button>
    </Grid>
  );
};

export default TodoInsert;
